<template>
    <div class="subcategory">

        <div class="container">
            <div class="home__products__item">
                <div class="section-head">
                    <h2>{{subcategory.title}}</h2>
                </div>
                <vue-element-loading :active="show" spinner="bar-fade-scale" :is-full-screen="true"/>
                <div class="home__products category__row">
                    <product-item :items="products"/>
                </div>
            </div>

        </div>

    </div>
</template>

<script>
import VueElementLoading from "vue-element-loading";
import axios from 'axios'
import productItem from '../components/product-item.vue'

export default {
    components:{
        productItem,
        VueElementLoading
    },
    data(){
        return{
            show:false,
            subcategory:{},
            id: this.$route.params['id'],
            products:[],
        }
    },
    computed:{
        BASIC_URL(){
            return this.$store.getters.getURL
        }
    },
    watch:{
        $route(to){
            this.show = true
            axios.get(`${this.BASIC_URL}/subcategory/`+to.params.id)
            .then(response =>{
                this.subcategory = response.data
                document.title = `${this.subcategory.title} | Altech - Ноутбуки, Принтеры и Компьютерная Техника в Ташкенте!`
                this.show = false

            })
            axios.get(`${this.BASIC_URL}/product/api/bycats/`+to.params.id)
            .then(response => {
                this.products = response.data
                this.show = false
            })
            this.$store.dispatch('changeRoute', false)
        }
    },
    created(){
        this.show = true
        axios.get(`${this.BASIC_URL}/subcategory/`+this.id)
        .then(response =>{
            this.subcategory = response.data
            document.title = `${this.subcategory.title} | Altech - Ноутбуки, Принтеры и Компьютерная Техника в Ташкенте!`
            this.show = false
        })
        axios.get(`${this.BASIC_URL}/product/api/bycats/`+this.id)
        .then(response => {
            this.products = response.data
            this.show = false
        })
    },
    mounted(){
        this.$store.dispatch('changeRoute', false)
    }
}
</script>

<style lang="less" scoped>
@import '../styles/category.less';
</style>