<template>
    <div class="subcategory">

        <div class="container">
            <div class="home__products__item">
                <div class="section-head">
                    <h2>{{category.title}}</h2>
                </div>

                <div class="home__products products-grid">
                    <product-item :items="category.products"/>
                </div>
            </div>

        </div>

    </div>
</template>

<script>
import axios from 'axios'
import productItem from '../components/product-item.vue'

export default {
    components:{
        productItem,
    },
    data(){
        return{
            category:{},
            id: this.$route.params['id'],
        }
    },
    computed:{
        BASIC_URL(){
            return this.$store.getters.getURL
        }
    },
    watch:{
        $route(to){
            axios.get(`${this.BASIC_URL}/brend/get/`+to.params.id)
            .then(response =>{
                this.category = response.data
                document.title = `${this.category.title} | Altech - Ноутбуки, Принтеры и Компьютерная Техника в Ташкенте!`
            })
        }
    },
    created(){
        axios.get(`${this.BASIC_URL}/brend/get/`+this.id)
        .then(response =>{
            this.category = response.data
            document.title = `${this.category.title} | Altech - Ноутбуки, Принтеры и Компьютерная Техника в Ташкенте!`
        })
    },
}
</script>

<style lang="less" scoped>
.subcategory{
    padding: 30px 0px;
    .container{
        .home__products__item{
            .section-head{
                padding: 0px 0px 20px;
            }
            .products-grid{
                display: grid;
                grid-template-columns: 1fr 1fr 1fr 1fr;
            }
        }
    }
}
@media(max-width:500px){
    .subcategory .container .home__products__item .products-grid{

        grid-template-columns: repeat(2, 1fr); grid-gap: 5px;
    }
}
</style>