<template>
    <div class="category">
        <vue-element-loading :active="show" spinner="bar-fade-scale" :is-full-screen="true"/>
        <div class="container">
            <div class="category__main">
                <div class="section-head">
                    <h2>{{category.title}}</h2>
                </div>

                <div class="category__main__grid" v-for="subcategory in category.subcategory" :key="subcategory._id">

                    <div class="section-head">
                        <h2>{{subcategory.title}}</h2>
                        <u><router-link :to="`/subcategory/${subcategory._id}`">Посмотреть всё</router-link></u>
                    </div>

                    <div class="category__row">
                        <product-item :items="subcategory.products"/>
                    </div>

                </div>
            </div>

        </div>

    </div>
</template>

<script>
import VueElementLoading from "vue-element-loading";
import axios from 'axios'
import productItem from '../components/product-item.vue'

export default {
    components:{
        productItem,
        VueElementLoading
    },
    data(){
        return{
            show:false,
            category:{},
            id: this.$route.params['id'],
        }
    },
    computed:{
        BASIC_URL(){
            return this.$store.getters.getURL
        }
    },
    watch:{
        $route(to){
            this.show = true
            axios.get(`${this.BASIC_URL}/category/get/`+to.params.id)
            .then(response =>{
                this.category = response.data
                document.title = `${this.category.title} | Altech - Ноутбуки, Принтеры и Компьютерная Техника в Ташкенте!`
                this.show = false
            })
        }
    },
    created(){
        this.show = true
        axios.get(`${this.BASIC_URL}/category/get/`+this.id)
        .then(response =>{
            this.category = response.data
            document.title = `${this.category.title} | Altech - Ноутбуки, Принтеры и Компьютерная Техника в Ташкенте!`
            this.show = false
        })
    },
}
</script>

<style lang="less" >
@import '../styles/category.less';
</style>