
import { createStore } from 'vuex'
import { home } from './modules/home'
import {loading} from './modules/loading'
import category  from './modules/category'
import brand  from './modules/brand'
const store = createStore({
    state: {
        statCat: false,
        statCart: false,
        statFav: false,
        BASIC_URL: 'https://wt.altech.uz',
        cart:[],
        favs:[]
    },
    getters:{
        getCountCartProducts(state){
            return state.cart.length
        },
        getCountFav(state){
            return state.favs.length
        },
        getCartProducts(state){
            return state.cart  
        },
        getCat(state){
            return state.statCat
        },
        getCart(state){
            return state.statCart
        },
        getFav(state){
            return state.statFav
        },
        getURL(state){
            return state.BASIC_URL
        },
        getFavs(state){
            return state.favs
        }
    },
    mutations: {
        addCartProduct(state,payload){
            state.cart.push(payload)
        },
        setCartProducts(state,payload){
            state.cart = payload
        },
        setRoute(state,payload){
            state.statCat = payload
        },
        setCart(state,payload){
            state.statCart = payload
        },
        setFav(state,payload){
            state.statFav = payload
        },
        setFavProducts(state,payload){
            state.favs = payload
        }
    },
    actions:{
        setFavProducts(context,payload){
            context.commit('setFavProducts',payload)
        },
        addCartProduct(context,payload){
            context.commit('addCartProduct',payload)
        },
        setCartProducts(context,payload){
            context.commit('setCartProducts',payload)
        },
        getCartProducts(context,payload){
            context.commit('setCartProducts',payload)
        },
        changeRoute(context,payload){
            context.commit('setRoute',payload)
        },
        showCart(context,payload){
            context.commit('setCart',payload)
        },
        showFav(context,payload){
            context.commit('setFav',payload)
        },
    },
    modules: {
        home, loading, category, brand
    }
})

export default store