<template>
    <header class="header">
        <div class="container">
            <div class="header__left">
                <router-link to="/" class="header__left__logo">
                    <img src="@/assets/img/logo.png" alt="">
                </router-link>
            </div>

            <div class="header__extra">

                <div class="header__catalog-btn">
                    <button @click="showCat()">
                        <i class="fa-solid fa-bars fa-lg"></i>
                        <p>Каталог</p>
                    </button>
                </div>

                <div :class="`header__mid ${searchtoggle?'active':''}`" v-click-outside="onClickOutside">
                    <input v-model="searchInput"  type="text" :placeholder="placeholder" @input="search($event.target.value)">
                    <button>
                        <i class="fa-solid fa-magnifying-glass fa-lg "></i>
                    </button>

                    <div  class="foundItems" :style="{display: foundItems.length == 0 || this.searchInput == '' ? 'none' : 'flex' }">
                        <div @click="moveboy(`/product/${foundItem._id}`)" class="foundItems__item" v-for="foundItem in foundItems" :key="foundItem">
                            {{foundItem.title}}
                            <span>{{foundItem.price}} сум</span>
                        </div>
                    </div>
                </div>

                <div class="header__right">
                    <button class="header__right__btn call" @click="searchtoggle = !searchtoggle">
                        <i class="fa-solid fa-magnifying-glass fa-lg" width="24" height="24"></i>
                    </button>
                    <button class="header__right__btn" @click="showFav()">
                        <i class="fa-solid fa-heart fa-lg" width="24" height="24"></i>
                        <span v-show="amountFav>0">{{amountFav}}</span>
                    </button>
                    
                    <a class="header__right__btn call" href="https://www.google.com/maps/place/ALTECH.UZ+-+%D0%9C%D0%B0%D0%B3%D0%B0%D0%B7%D0%B8%D0%BD+%D0%9A%D0%BE%D0%BC%D0%BF%D1%8C%D1%8E%D1%82%D0%B5%D1%80%D0%BD%D0%BE%D0%B9+%D1%82%D0%B5%D1%85%D0%BD%D0%B8%D0%BA%D0%B8+%D0%B8+%D0%92%D0%B8%D0%B4%D0%B5%D0%BE%D0%BD%D0%B0%D0%B1%D0%BB%D1%8E%D0%B4%D0%B5%D0%BD%D0%B8%D1%8F!/@41.2760858,69.2211539,15z/data=!4m5!3m4!1s0x0:0xf8d31a5552257074!8m2!3d41.2760858!4d69.2211539">
                        <i class="fas fa-map-marker-alt fa-lg"></i>
                    </a>
                    <a href="tel:+998712003223" class="header__right__btn call">
                        <i class="fa-solid fa-phone"></i>
                    </a>
                </div>
<!--                <router-link to="/quiz" class="giftmob"><i class="fa-solid fa-gift"></i></router-link>-->
            </div>

            <div class="header__catalog" v-if="isActive" v-bind:class="{ show: isActive }" v-click-outside="outsideCatalog">
                <div class="container">
                    <div class="sidemenu__left">
                        <div class="sidemenu__left__item" v-for="(cat, index) in cats" :key="index" >
                            <span @mouseenter="catRight = cat.subcategories" @click="catRight = cat.subcategories" :class="{show: cat.subcategories == catRight}"> 
                                <img :src="`${BASIC_URL}/${cat.icon}`" alt="">{{cat.title}} 
                            </span>
                        </div>
                    </div>  
                    <div class="sidemenu__right" :class="{show_mobile: catRight.length > 0}">
                        <button class="sidemenu__right__back" @click="catRight = [] ">
                            <i class="fa-solid fa-arrow-left"></i>
                        </button>

                        <ul :class="{'flex':catRight.length>6,'sidemenu__right__item':true}">
                            <li v-for="item in catRight" :key="item" >
                                <router-link :to="`/subcategory/${item._id}`">{{item.title}}</router-link>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>


        </div>
    </header>
</template>

<script>
import axios from 'axios'
import vClickOutside from 'click-outside-vue3'

    export default {
        data() {
            return {
                hover: '',
                cats: [
                    {
                        title: ''    
                    }
                ],
                catRight:[],
                foundItems:[],
                searchInput: '',
                headerCats:[],
                products:[],
                list:[],
                placeholder:'',
                count:0,
                timer:null,
                searchtoggle:false
            }
        },
        computed: {
            amountFav(){
                return this.$store.getters.getCountFav
            },
            amountCart(){
                return this.$store.getters.getCountCartProducts
            },
            isActive(){
                return this.$store.getters.getCat
            },
            BASIC_URL(){
                return this.$store.getters.getURL
            }
        },
        methods: {
            moveboy(link){
                this.searchInput = ''
                this.foundItems = []
                this.$router.push(link)
            },
            search(title){
                if (title.length>0) {
                    axios.get(`${this.BASIC_URL}/product/search/${title}`)
                    .then(res => { 
                        if (res.data.length > 0 && res.data !=='error') {
                            this.foundItems = res.data
                        } else {
                            this.foundItems = []
                        }
                    })
                }
            },
            showCat() {
                this.$store.dispatch('changeRoute', !this.isActive)
            },
            onClickOutside () {
                this.searchInput = ''
                this.foundItems = []
            },
            outsideCatalog(){
                this.$store.dispatch('changeRoute', false)
            },
            showCart() {
                this.$store.dispatch('showCart', !this.isActive)
                this.$store.dispatch('showFav', false)
            },
            showFav() {
                this.$store.dispatch('showFav', !this.isActive)
                this.$store.dispatch('showCart', false)
            },
            changePlaceholder(){
                if (this.count == 10) this.count = 0

            }
        },
        directives: {
            clickOutside: vClickOutside.directive
        },
        created(){
            axios.get(`${this.BASIC_URL}/header/`).then(response =>{
                this.cats = response.data
            })
            axios.get(`${this.BASIC_URL}/product/api/random`).then(response =>{
                
                this.products = response.data
                if (this.products.length>0){
                    this.products.forEach(product => {
                        this.list.push(product.title)
                    })
                    let n = 0
                    
                    this.timer = setInterval(()=>{
                        if (this.placeholder == this.list[this.count]){
                            this.count++ 
                            n = 0
                            if (this.count == 10) this.count = 0
                            this.placeholder = ''
                        }
                        this.placeholder += this.list[this.count][n]
                        n++
                    },200)
                }
            })
        },
        unmounted(){
            clearInterval(this.timer)
        }
    }

</script>

<style lang="less" scoped>
@import '../styles/var.less';
.giftmob {
    position: fixed;
    right: 60px;
    bottom: 60px;
    background-color: rgb(223, 29, 29);
    width: 80px;
    height: 80px;
    border-radius: 50%;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 24px;
    z-index: 1;
    &:before {
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        z-index: -1;
        background-color: rgb(223, 29, 29);
        display: inline-block;
        border-radius: 50%;
        animation-name:gift;
        animation-iteration-count: infinite;
        animation-duration:1s;
    }
}
    
    @keyframes gift {
        0% {
            transform: scale(1);
            opacity: 1;
        }
        100%{
            transform: scale(1.5);
            opacity: 0;
        }
    }
    .header {
        background-color: @blue;
        padding-top: 15px;
        padding-bottom: 15px;
        position: sticky;
        top: 0;
        left: 0;
        width: 100%;
        z-index: 8;
        .container {
            display: flex;
            align-items: center;
            justify-content: space-between;
        }
        &__left {
            flex-basis: 20%;
            width: 20%;
            display: flex;
            align-items: center;
            justify-content: space-between;
            &__logo {
                img {
                    width: 260px;
                    filter:brightness(0) invert(1);
                }
            }
        }
        &__extra {
            flex-basis: 80%;
            width: 80%;
            display: flex;
            align-items: center;
        }
        &__catalog-btn {
            button {
                font-size: 16px;
                padding: 11.5px 20px;
                display: flex;
                align-items: center;
                color: #fff;
                background-color: darken(@blue,10%);
                i{
                    &::before{
                        width: 40px;
                    }
                    margin-right: 10px;
                    color: #fff;
                }
                &:hover {
                    background-color: lighten(#008dff, 5%);
                }
            }
        }
        &__mid {
            flex-basis: 90%;
            width: 90%;
            padding: 0px 10px;
            display: flex;
            position: relative;
            input {
                flex:1;
                height: 100%;
                padding: 11px;
                transition: .2s ease-in-out;
                border-right: 0;
                border-top-right-radius: 0;
                border-bottom-right-radius: 0;
                border:0;
            }
            button {
                padding: 10px;
                display: flex;
                align-items: center;
                justify-content: center;
                border-top-left-radius: 0;
                border-bottom-left-radius: 0;
                background: #fff;
                color: #000;
                .fa-magnifying-glass{
                    color: rgb(73, 73, 73);    
                }
                &:hover {
                    background-color: lighten(#008dff, 5%);
                }
            }
            .foundItems{
                position: absolute;
                background: #fff;
                top: 100%; left: 0;
                margin: 0px 10px;
                
                width: -webkit-fill-available; max-height: 300px;
                overflow: auto;
                box-shadow: 0px 4px 12px 1px #78787847;
                border-radius: 6px;
                display: flex; flex-direction: column;
                z-index: 10;
                &__item{
                    padding:15px 10px;
                    display: flex;
                    width: 100%;
                    justify-content: space-between;
                    cursor: pointer;
                    span {
                        font-weight: 600;
                    }
                    &:hover {
                        background-color: #eee;
                    }
                }
            }
        }
        &__right {
            padding-left: 10px;
            height: 100%;
            display: flex;
            justify-content: space-around;
            &__btn {
                padding: 10px;
                display: flex;
                align-items: center;
                justify-content: center;
                position: relative;
                background: 0;
                color: #fff;
                border-radius: 0px;
                font-size: 22px;
                span {
                    position: absolute;
                    background-color: var(--main-color);
                    color: #fff;
                    font-size: 14px;
                    width: 20px;
                    height: 20px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    border-radius: 50%;
                    top: -10%;
                    right: -60%;
                }
                &.call {
                    display: none;
                }
            }
            
        }
        &__catalog {
            padding: 0;
            position: absolute; 
            width: 100%; max-height: 0;
            overflow: hidden;
            background-color: #fff;
            top: 74px; left: 0;
            transition: ease .3s;
            border-radius: 6px;
            box-shadow: 0px 20px 20px 0px #1a1a1a13;
            z-index: 9;
            .container{
                display: flex;
                align-items: normal; 
                justify-content: flex-start;
                height: 100%;
                .sidemenu__left {
                    height: 100%;
                    width: 25%;
                    padding: 20px 20px 20px 0px;

                    &__item {
                        width: 100%;
                        display: flex;
                        span{
                            padding: 10px;
                            padding-right: 30px;
                            width: 100%;
                            transition: .1s ease-in-out;
                            display: flex;
                            align-items: center;
                            border-radius: 5px;
                            &:hover {
                                img {
                                    filter:brightness(0) invert(1);
                                }
                            }
                            img {
                                width:40px;
                                margin-right: 10px;
                                opacity: 0.6;
                                
                            }
                            &.show{
                                background-color: var(--main-color);
                                color: #fff;
                                img {
                                    filter:brightness(0) invert(1);
                                    opacity: 1;
                                }
                            }
                        }
                    }
                }
                .sidemenu__right {
                    flex-basis: 68%;
                    width: 68%;
                    margin-right: -100%;
                    background-color: #fff;
                    padding-bottom: 50px;
                    border-left: 1px solid #eeeeee;
                    padding: 20px;
                    &__back{
                        display: none;
                    }
                    &__item {
                        padding: 10px;
                        &.flex{
                            display: flex;
                            flex-wrap: wrap;
                            li {
                                flex-basis: 33.33%;
                                max-width: 33.33%;
                            }
                        }
                        li {
                            margin-bottom: 20px;
                            a{
                                padding: 10px;
                                border-radius: 5px;
                                &:hover {
                                    background-color: @blue;
                                    color: #fff;
                                }
                            }
                        }
                    }
                }
            }
            &.show{
                max-height: 100vh;
                height: auto;
            }
        }
    }
    @media (max-width:500px){
        .header .container .header__catalog .container .sidemenu__right__item.flex li{
            flex-basis: 100%;
            max-width: 100%;
        }
        .giftmob{
            right: 30px;
            bottom: 70px;
            width: 60px;
            height: 60px;
        }
        .header {
            padding: 10px 0px;
            &__left {
                &__logo{
                    width: 240px;
                    img {
                        width: 100%;
                    }
                }
            }
        }
    }
    @media (max-width: 475px) {
        .header {
            .container {
                .header__left {
                    justify-content: center;
                    width: 100%;
                    flex-basis: 100%;
                    &__logo {
                        img {
                            image-rendering: -webkit-optimize-contrast;
                        }
                    }
                    &__btn {
                        display: none;
                    }
                }
                .header__extra {
                    position: fixed;
                    bottom: 0;
                    left: 0;
                    width: 100%;
                    background-color: #fff;
                    padding: 0;
                    border-top-left-radius: 10px;
                    border-top-right-radius: 10px;
                    z-index: 5;
                    // left-shadow: 0px 16px 20px 8px #565656;
                    .header__mid {
                        position: fixed;
                        max-height: 0;
                        padding:0px 10px;
                        left: 0;
                        width: 100%;
                        transition: 0.3s;
                        background-color: #fff;
                        overflow: hidden;
                        top: 60px;
                        .foundItems {
                            margin: 0;
                            &__item{
                                display: block;
                                padding: 10px;
                                span {
                                    display: block;
                                }
                            }
                        }
                        &.active {
                            overflow: visible;
                            padding-top: 10px;
                            padding-bottom: 10px;
                            max-height: unset;
                        }
                    }
                    .header__catalog-btn {
                        flex-basis: 25%;
                        width: 25%;
                        background: 0;
                        padding: 0;
                        color: #000;
                        button {
                            display: block;
                            width: 100%;
                            padding: 15px 0;
                            background: 0;
                            &:hover {
                                background-color: unset;
                            }
                            .fa-bars {
                                color: #000;
                                margin: 0;
                            }
                            p {
                                display: none;
                            }
                        }
                    }
                    .header__right {
                        flex-basis: 75%;
                        width: 75%;

                        &__btn {
                            flex-basis: 33%;
                            width: 33%;
                            padding: 15px 0px;
                            margin: -15px;
                            color: #000;
                            font-size: 16px;
                            &.call {
                                display: flex;
                            }

                            span {
                                font-size: 10px;
                                width: 15px;
                                height: 15px;
                                top: 15%;
                                right: 30%;
                            }
                        }
                    }
                }
                .header__catalog{
                    z-index: 2;
                    top: 60px;
                    left: 0;
                    height: 100vh;
                    width: 0;
                    white-space: pre;
                    &.show{
                        position: fixed;
                        left: 0;
                        width: 100%;
                        height: 100vh;
                    }
                    .container{
                        .sidemenu__left{
                            width: 100%;
                            z-index: 1;
                            padding: 60px 0;
                        }
                        .sidemenu__right{
                            position: absolute;
                            top: 0;
                            left: -100vw;
                            z-index: 2;
                            transition: .3s ease;
                            width: 100%;
                            padding: 60px 0;
                            &__back{
                                display: block;
                                position: absolute;
                                top: 10px; left: 10px;
                                background: 0;
                                padding: 15px;
                                margin-left: -10px;
                            }
                            &.show_mobile{
                                left: 0;
                                overflow: auto;
                                padding-bottom: 120px;
                            }
                        }
                    }
                }
            }
        }
    }
</style>