<template>
    <div class="home">
        <div class="category">
            <div class="container">
                <div class="category__row">
                    <div class="category__box" v-for="c of category" :key="c._id">
                        <router-link class="category__item"  :to="`/category/${c._id}`">
                            <span>{{c.title}}</span>
                            <img :src="`${BASIC_URL}/${c.img}`" alt="">
                        </router-link>
                    </div>
                </div>
            </div>
        </div>
        <div class="home__top">
            <div class="container">
            <section class="home__slider">
                <div class="home__left" v-if="sale.length>0">
                    <div class="home__title">Супер распродажа!</div>
                    <Splide class="splide" :options=" {
                        type: 'loop', 
                        rewind: true,
                        arrows:false,
                        pagination:false, 
                        cover: false, 
                        autoplay: true, 
                        interval:4000,
                        paginationKeyboard:false,
                        keyboard:false,
                        classes: {
                            arrow : 'splide__arrow arrow',
                            pagination: 'splide__pagination pagination',
                            page: 'splide__pagination__page pagination__item' },
                        breakpoints: {
                            475: {
                                arrows: false
                            },
                        }
                    }">
                        <SplideSlide v-for="item of sale"  :key="item._id" class="home__slide">
                            <oneproductVue :item="item"/>
                        </SplideSlide>
                    </Splide>
                </div>
                <Splide class="splide" :options=" {
                    type: 'loop', 
                    rewind: true, 
                    cover: false, 
                    autoplay: false, 
                    paginationKeyboard:false,
                    keyboard:false,
                    classes: {
                        arrow : 'splide__arrow arrow',
                        pagination: 'splide__pagination pagination',
                        page: 'splide__pagination__page pagination__item' },
                    breakpoints: {
                        475: {
                            arrows: false
                        },
                    }
                    }">
                    <SplideSlide class="home__product" :style="{
                        backgroundImage: `url(${require('@/assets/img/slider/slider-main-1.jpg')})`
                        }">
                    </SplideSlide>
                </Splide>
            </section>
            </div>
        </div>
        <div class="container">
            <section class="home__products">
                
                <div class="home__products__item">
                    <div class="section-head">
                        <h2>Последные товары</h2>
                    </div>
                    <div class="home__products__grid products-grid">
                        <product-item :items="products"/>
                    </div>
                </div>
            </section>
        </div>
        <div class="brands">
            <div class="container">
                <h2>Бренды</h2>
                <div class="brands__row">
                    <div class="brands__item" v-for="brand of brands" :key="brand._id">
                        <router-link :to="`/brand/${brand._id}`" >
                            <img :src="`${BASIC_URL}/${brand.img}`" alt="">
                        </router-link>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>

import productItem from '../components/product-item.vue'
import oneproductVue from "../components/oneproduct.vue";
export default {
    components:{
        productItem,
        oneproductVue
    },
    data(){
        return{
            show:true,
            subcategories:[],
            bottom: false,
            count:0,
            end: false,
        }
    },
    watch: {
        $route(){
            this.count = 0
            this.show = true
            document.title = 'Altech - Ноутбуки, Принтеры и Компьютерная Техника в Ташкенте!'
        }
    },
    
    computed: {
        BASIC_URL(){
            return this.$store.getters.getURL
        },
        sale(){
            return this.$store.getters.getSale
        },
        products(){
            return this.$store.getters.products
        },
        brands(){
            return this.$store.getters.brand
        },
        category(){
            return this.$store.getters.category
        }
    },
    created() {
        this.$store.dispatch('setSale')
        document.title = 'Altech - Ноутбуки, Принтеры и Компьютерная Техника в Ташкенте!'
        this.$store.dispatch('last')
        this.$store.dispatch('brand')
        this.$store.dispatch('category')
    },

}

</script>

<style lang="less">
@import '../styles/home.less';
</style>