<template>
    <div class="fav" v-bind:class="{ show: isActive }" v-if="isActive" v-click-outside="outsideFav">
        <div class="fav__box">
            <div class="fav__box__top">
                <button class="close" @click="showFav()">
                    <i class="fas fa-times fa-2x"></i>
                </button>
                <div class="title">Избранное</div>
            </div>

            <div class="fav__box__bottom">
                <div class="fav-item" v-for="(item,index) in fav" :key="item">
                    <button class="fav__remove" @click="remove(index)"><i class="fa fa-times"></i></button>
                    <div class="fav-item__left">
                        <div class="img">
                            <img :src="`${this.BASIC_URL}/`+item.img">
                        </div>
                    </div>

                    <div class="fav-item__right">
                        <router-link :to="`/product/${item._id}`"  class="title">{{item.title}}</router-link>
                        <div class="price">{{item.price}} сум</div>
                    </div>

                </div>
                
            </div>
        </div>
    </div>
</template>

<script>
import vClickOutside from 'click-outside-vue3'

export default {
    data(){
        return{ 
        }   
    },
    computed: {
        isActive(){
            return this.$store.getters.getFav
        },
        BASIC_URL(){
            return this.$store.getters.getURL
        },
        fav(){
            return this.$store.getters.getFavs
        }
    },
    directives: {
        clickOutside: vClickOutside.directive
    },
    methods:{
        showFav() {
            this.$store.dispatch('showFav', false)
        },
        remove(index){
            this.fav.splice(index,1)
            this.$store.dispatch('setFavProducts',this.fav)
            this.$cookies.set('fav',JSON.stringify(this.fav))
        },
        outsideFav(){
            this.$store.dispatch('showFav', false)
        }
    },
    mounted(){
        if (this.$cookies.isKey('fav')){
            this.$store.dispatch('setFavProducts',JSON.parse(this.$cookies.get('fav')))
        }
    }
}
</script>

<style lang="less" scoped>
.fav__remove{
    position: absolute;
    right: 5px;
    top: 5px;
    background: unset;
    opacity: 0;
    transition: 0.5s;
}
.fav{
    position: fixed;
    top: 0; right: -100%;
    width: 25%; height: 100vh;
    background-color: #fff;
    z-index: 9;
    transition: .3s ease;
    box-shadow: 0px 0px 20px 0px #46464654;
    &.show{
        right: 0;
    }
    &__box{
        padding: 10px;
        display: flex; flex-direction: column;
        height: 100%;
        &__top{
            flex-basis: 5%;
            display: flex; align-items: center;
            .close{
                background: 0;
                flex-basis: 20%;
                display: flex;
            }
            .title{
                flex-basis: 80%;
                font-size: 20px;
                font-weight: 600;
            }
        }
        &__bottom{
            flex-basis: 90%;
            overflow-y: scroll;
            .fav-item{
                display: flex;
                border-bottom: 1px solid #e4e4e4;
                transition: .3s;
                position: relative;
                &:hover {
                    .fav__remove{
                        opacity: 1;
                    }
                }
                &__left{
                    flex-basis: 20%;
                    width: 20%;
                    .img{
                        display: flex;
                        img{
                            width: 100%;
                        }
                    }
                }
                &__right{
                    flex-basis: 80%;
                    width: 80%;
                    padding: 10px;
                    display: flex; flex-direction: column; justify-content: space-between;
                    .title{
                        font-size: 18px;
                        font-weight: 600;
                    }
                }
                &:hover{
                    background-color: rgb(243, 243, 243);
                }
            }
        }
    }
}

@media (max-width: 475px){
    .fav{
        z-index: 7;
        &.show{
            width: 100%;
        }
        &__box{
            padding: 70px 30px;
        }
    }
}
</style>