import axios from "axios"

const brand = {
    state: () => ({
        brand:[]
    }),
    getters:{
        brand(state){
            console.log(state.cat)
            return state.brand
        }
    },
    mutations:{
        brand(state,payload){
            console.log(payload)
            state.brand = payload
        }
    },
    actions:{
        brand(context){
            axios.get(`${context.getters.getURL}/brend/forhome`)
            .then(res => {
                console.log(res.data)
                context.commit('brand',res.data)
            })
            
        }
    }
}

export default brand