<template>
<div class="container">   
    <div class="product">
        <div class="product__row">
            <h2 class="product__title">{{item.title}}</h2>
            <div class="product__btns">
                <button @click="addFav(item)" class="fav">
                    <i class="fa-solid fa-heart fa-lg"></i> В избранное
                </button>
            </div>
        </div>
        <div class="product__top__right__nostock" v-show="item.status==2">Нет в наличие</div>
        <div class="product__top">
            <div class="product__top__left">
                <Splide class="main-slider"  
                    :options="{
                        type: 'loop', 
                        rewind: true, 
                        autoplay: true,
                        interval: 3000,
                        arrows: false,
                        classes: {
                            arrow : 'splide__arrow arrow',
                            pagination: 'splide__pagination pagination',
                            page: 'splide__pagination__page pagination__item' },
                        breakpoints: {
                            475: {
                                arrows: false
                            },
                        }
                    }">
                    <SplideSlide v-for="(photo,index) of item.photos" :key="index">
                        <img :src="`${this.BASIC_URL}/`+photo">
                    </SplideSlide>
                </Splide>
            </div>
            <div class="product__infolist">
                
                <div v-for="(at, index) of item.atribut" :key="index">
                    <span class="left">{{at.atr.title}}:</span>
                    <span class="right">{{at.spec}}</span>    
                </div>
                <div class="product__desc">
                    <div class="product__desc--title">Описание</div>
                    <div class="product__desc--text" v-html="item.descriptions"></div>
                </div>
            </div>
            <div class="product__top__right">
                
                <div class="product__top__right__price">
                    <p class="price">{{item.price.toLocaleString()}} сум <span>(цена без НДС)</span></p>
                </div>
                <div class="product__delivery">
                    <i class="fas fa-truck"></i>
                    <span>Доставка курьером:</span>
                    <b>Бесплатно</b>
                </div>
                <div class="product__top__right__buy">
                    <button class="buy" @click="showModal()">Купить</button>
                </div>

                <div class="product__top__right__info">
                    <div class="info-item">
                        <div class="info-left">
                            <div class="info-title">
                                <i class="fa-solid fa-message fa-lg"></i>
                                <b>Есть вопросы?</b>
                            </div>
                        </div>
                        <div class="info-right">
                            <a href="tel:+998712003223">
                                <i class="fa-solid fa-phone fa-lg   "></i>
                                <p>+99871 200 32 23</p>
                            </a>
                            <a href="https://t.me/altech_shop">
                                <i class="fa-brands fa-telegram fa-lg"></i>
                                <p>@altech_shop</p>
                            </a>
                            <a href="mailto:info@altech.uz">
                                <i class="fa-solid fa-at fa-lg"></i>
                                <p>info@altech.uz</p>
                            </a>
                        </div>
                    </div>
                    <div class="info-item">
                        <div class="info-title">
                            <i class="fas fa-info fa-lg"></i>
                            <div class="info-text">
                                <p>- Самые качественные товары и оптимальные <b>цены</b></p>
                                <p>- Доставка в область свыше 1 млн сумов - <b>бесплатно</b> </p>
                                <p>- Доставка по городу Ташкент - <b>бесплатно</b></p>
                                <p>- Гарантия от <b>3 - 60 месяцев</b></p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div v-if="modal" :class="{'show':modal,'modal':true}" id="modal" >
                <div class="modal__box" v-click-outside="onClickOutside">

                    <div class="modal__form" v-if="!accept">
                        <div class="close" @click="onClickOutside()">
                            <i class="fas fa-times fa-lg" ></i>
                        </div>
                        <div class="title">Купить в один клик</div>
                        <div class="phone">
                            <div class="phone__top">Номер телефона</div>
                            <input v-maska="'+998 (##) ###-##-##'" class="phone__bot" placeholder="+998 (__) __ __ __" v-model.trim="phone"/>
                        </div>
                        <button :class="phone.length == '19' ? 'yes' : '' " class="confirm" @click="send()">Оформить заказ</button>
                    </div>

                    <div class="modal__accept" v-else >
                        <div class="tick">
                            <i class="fas fa-check-circle fa-2x"></i>
                        </div>
                        <div class="accepted">Ваша заявка принята <br> Ждите звонка!</div>
                    </div>
                </div>

            </div>
        </div>

        <!-- <div class="product__bottom">

            <div class="product__bottom__title">
                <h2>Характеристики</h2>
            </div>

            <div class="product__bottom__specs">
                <div class="product__bottom__specs__item">
                    
                </div>
            </div>

        </div> -->

    </div>
    <div class="subcategory mb">

            <div class="home__products__item">
                <div class="section-head">
                    <h2>Другие товары</h2>
                </div>

                <div class="home__products category__row">
                    <product-item :items="others"/>
                </div>
            </div>



    </div>
</div>
</template>

<script>
import axios from 'axios';
import productItem from '../components/product-item.vue'
import vClickOutside from 'click-outside-vue3'

export default {
    components:{
        productItem
    },
    data(){
        return{
            show:false,
            item:{
                price: 0,
            },
            others:[],
            itemPrice: {},
            id: this.$route.params['id'],
            modal: false,
            phone: '+998',
            accept: false
        }
    },
    created(){
        this.$store.dispatch('loading',true)
        axios.get(`${this.BASIC_URL}/product/view/`+this.id).then(response => {
            this.item = response.data.product
            console.log(response.data.product)
            document.title = `${this.item.title} ${this.item.price} сум Altech - Ноутбуки, Принтеры и Компьютерная Техника в Ташкенте!`
            this.item.atribut = this.item.atribut.filter(atr => {
                if (atr.spec) {
                    return atr
                }
            })
            this.others = response.data.others
            this.$store.dispatch('loading',false)
        })
    },
    computed:{
        BASIC_URL(){
            return this.$store.getters.getURL
        }
    },
    directives: {
        clickOutside: vClickOutside.directive
    },
    methods:{
        addFav(item){
            let products = []
            if (this.$cookies.isKey('fav'))
                products = JSON.parse(this.$cookies.get('fav'))
            let index = products.findIndex(product => product._id == item._id)
            if (index == -1) products.push({
                title: item.title,
                price: item.price,
                img: item.photos[0],
                _id: item._id
            })
            this.$store.dispatch('setFavProducts',products)
            this.$cookies.set('fav',JSON.stringify(products))
        },
        addCart(item){
            let products = []
            if (this.$cookies.isKey('cart'))
                products = JSON.parse(this.$cookies.get('cart'))
            let index = products.findIndex(product => product.pro._id == item._id)
            let pro = {
                title: item.title,
                price: item.price,
                img: item.photos[0],
                _id: item._id,
            }
            if (index !== -1) {
                
                products[index].count += 1
            }
            else {
                products.push({pro,count:1})
            }
            this.$store.dispatch('setCartProducts',products)
            this.$cookies.set('cart',JSON.stringify(products))
        },
        showModal(){
            this.modal = !this.modal
            document.body.style.overflow = 'hidden' 
        },
        onClickOutside () {
            this.accept = false
            this.modal = false
            document.body.style.overflow = '' 
        },
        send(){
            axios.post(`${this.BASIC_URL}/oneclick/`,{
                phone: this.phone,
                product: this.id,
            }).then(res => {
                if (res.data == 'ok') {
                    this.accept = true
                }
            })
            this.phone = ''
        }
    },
    watch:{
        $route(to){
            this.show = true
            axios.get(`${this.BASIC_URL}/product/view/`+to.params.id).then(response => {
                this.item = response.data.product
                document.title = `${this.item.title} ${this.item.price} сум | Altech - Ноутбуки, Принтеры и Компьютерная Техника в Ташкенте!`
                this.others = response.data.others
                this.show = false
            })
        },

    },
    mounted(){
        this.$store.dispatch('changeRoute', false)
    }
}
</script>

<style lang="less">
