    margin-bottom: 80px;
}
.info-text p {
    margin-bottom: 15px;
}
.product{
    margin-top: 30px;
    background-color: #fff;
    padding: 40px;
    &__desc {
        border-top: 1px solid rgb(77, 77, 77);
        padding-top: 10px;
        &--text {
            background-color: #ebebeb49;
            padding: 10px;
            font-size: 12px;
            line-height: 22px;
        }
        &--title {
            font-weight: 600;
        }
    }
    &__delivery {
        font-size: 16px;
        display: flex;
        align-items: center;
