                            &:last-child{
                                margin: 0;
                            }
                        }
                    }
                }
            }
        }
        .modal{
            position: fixed;
            top: 0; left: 0;
            z-index: 10;
            width: 100vw; height: 100vh;
            background-color: rgba(0, 0, 0, 0.349);
            overflow: hidden;
            display: none;
            &.show{
                display: flex; align-items: center; justify-content: center;
            }
            &__accept{
                text-align: center;
                .tick{
                    margin-bottom: 10px;
                    i{
