                }
            }
            &__box{
                padding: 30px;
                background-color: #fff;
                border-radius: 6px;
                display: flex; flex-direction: column;
                position: relative;
                .close{
                    align-self: flex-end;
                    width: 30px; height: 30px;
                    cursor: pointer;
