import axios from "axios"

export const home = {
    state: () => ({ 
        sale:[],
        BASIC_URL: 'https://wt.altech.uz',
        products:[],
    }),
    getters: { 
        getSale(state){
            return state.sale
        },
        products(state){
            return state.products
        }
    },
    mutations: { 
        setSale(state,payload){
            state.sale = payload
        },
        products(state,payload){
            state.products = payload
        }
    },
    actions: { 
        setSale(context){
            axios.get(`${context.state.BASIC_URL}/product/sale`)
            .then(res => {
                context.commit('setSale',res.data)
            })
        },
        last(context){
            context.commit('loading',true)
            axios.get(`${context.state.BASIC_URL}/product/last`)
            .then(res => {
                console.log(res.data)
                context.commit('products',res.data)
                context.commit('loading',false)
            })
        }
    },
    
}