<template>
    <footer class="footer">

        <div class="container">
            <div class="footer__top">
                <div class="footer__top__item" >
                    <p>{{footer.category.title}}</p>
                    <router-link :to="`/category/${item._id}`" v-for="item in footer.category.categories" :key="item">{{item.title}}</router-link>
                </div>
                <div class="footer__top__item half" >
                    <p>{{footer.subcategory.title}}</p>
                    <router-link :to="`/subcategory/${item._id}`" v-for="item in footer.subcategory.subcategories" :key="item">{{item.title}}</router-link>
                </div>
                <div class="footer__top__item half" >
                    <p>{{footer.brend.title}}</p>
                    <router-link :to="`/brand/${item._id}`" v-for="item in footer.brend.brends" :key="item">{{item.title}}</router-link>
                </div>
                <!-- <div class="footer__top__item">
                    <p>Мы</p>
                    <router-link to="/">О нас</router-link>
                    <router-link to="/">Блог</router-link>
                    <router-link to="/">Контакты</router-link>
                </div> -->
            </div>
            <div class="footer__bottom">
                <div class="left">© Since 2021</div>
                <span class="divider"></span>
                <div class="right">Сделано в <u><a href="doin.uz">Doin.uz</a></u></div>
            </div>
        </div>


    </footer>
</template>

<script>
import axios from 'axios'

export default {
    data(){
        return{
            footer:{
                
                subcategory:{
                    title: ''
                },
                category:{
                    title: ''
                },
                brend:{
                    title: ''
                }
            }
        }
    },
    computed: {
        BASIC_URL(){
            return this.$store.getters.getURL
        }
    },
    created(){
            axios.get(`${this.BASIC_URL}/footer`).then(response =>{
            this.footer = response.data
        })
    }
}
</script>

<style lang="less" scoped>
.footer{
    background-color: #2c2c2c;
    color: #fff;
    padding-top: 50px;
    padding-bottom: 20px;
    a{
        color: #fff;
    }
    .container{
        .footer__top{
            display: flex; justify-content: space-between;
            padding-bottom: 30px;
            border-bottom: 1px solid #fff;
            &__item{
                margin-right: 100px;
                flex-basis: 33%;
                display: flex; flex-direction: column;
                &.half {
                    flex-direction: row;
                    flex-wrap: wrap;
                    p {
                        flex-basis: 100%;
                    }
                    a {
                        flex-basis: 50%;
                    }
                }
                &:last-child{
                    margin: 0;
                }
                p{
                    margin-bottom: 10px;
                    color: rgb(179, 179, 179);
                }
                a{
                    width: fit-content;
                    margin-bottom: 10px;
                    font-size: 14px;
                    transition: .3s ease-in-out;
                    &:hover{
                        color: var(--main-color);
                    }
                }
            }
        }
        .footer__bottom{
            padding-top: 20px;
            display: flex; align-items: center; justify-content: center;
            color: #fff;
            .divider{
                width: 1px;
                height: 15px;
                background-color: #fff;
                margin: 0px 10px;
            }
        }
    }
    }

@media (max-width: 475px) {
    .footer{
        padding-bottom: 10px;
        .container{
            .footer__top{
                flex-direction: column;
                &__item{
                    margin-right: 0;
                    p{
                        font-size: 14px;
                        margin: 10px 0;
                    }
                    a{
                        margin-bottom: 10px;
                        font-size: 12px;
                        transition: .3s ease-in-out;
                        &:hover{
                            color: var(--main-color);
                        }
                    }
                }
            }
            .footer__bottom{
                padding-top: 10px;  
                font-size: 14px;
            }
        }
    }
}
</style>